
export const isTokenExpired = (token) => {
  if (!token) return true;

  try {
    const payload = JSON.parse(atob(token.split('.')[1]));
    const currentTime = Date.now() / 1000;


      return payload.exp < currentTime;
    


  } catch (error) {
    console.error('Error decoding token:', error);
    return true;
  }
};


